<template>
    <div class="roles-list">
        <ul class="items">
            <li v-for="(item, id) in data.items"
                :class="{ active: props.id === item.code }"
                v-show="item.code !== 'user'"
                @click="emit('change', item)"
            >
                <span>{{ t(`constants.roles.${ item.code }`) }}</span>
            </li>
        </ul>

        <Button :loading="data.loading"
            class="tertiary size-l"
            :name="t('blocks.roles.load')"
            v-if="data.loading || (data.items.length < data.total)"
        />
    </div>
</template>

<script setup>
    import { useI18n } from 'vue-i18n';
    import { onMounted, reactive } from 'vue';
    import api from 'ðŸ”—/api';
    import Button from 'ðŸ”—/components/ui/Button.vue';

    const emit = defineEmits(['change']);

    const { t } = useI18n();
    const data = reactive({
        items: [],
        loading: false,
        skip: 0,
        take: 20,
        total: 0
    });

    const getRoles = async () => {
        data.loading = true;

        const roles = await api.roles().list({
            skip: data.skip,
            take: data.take
        });


        setTimeout(() => {
            data.items.splice(data.items.length, 0, ...roles);
            data.total = data.items.length;

            data.loading = false;
        }, 200);
    };

    const props = defineProps({
        title: { type: String, default: '' },
        id: { type: [String, null], default: null }
    });

    onMounted(() => {
        getRoles();
    });
</script>

<style lang="scss" scoped>
    .roles-list {
        width: 30%;
        max-width: 320px;
        flex: 1 1 100%;
        display: flex;
        flex-direction: column;
        border-right: solid 1px var(--secondary-5);
        // border-left: solid 1px var(--secondary-5);
        .items {
            display: flex;
            margin: 0;
            padding: 64px 0 0;
            list-style: none;
            flex-direction: column;
            li {
                display: flex;
                min-height: 45px;
                align-items: center;
                cursor: pointer;
                padding: 0 8px;
                & > span {
                    font: var(--font-body-1);
                    color: var(--dark-500);
                }


                &.active {
                    background: var(--light-20);
                    & > span {
                        font-weight: bold;
                    }
                }

                &:hover {
                    background: var(--light-10);
                    & > span {
                        font-weight: bold;
                    }
                }
            }
        }
        .loader {
            margin: auto;
        }
    }
</style>