<template>
    <PageHeader :title="t('pages.roles.title')">
        <Button
            icon="plus"
            @click=""
            class="size-s"
            :name="t('pages.roles.add')"
            disabled
        />
    </PageHeader>

    <div class="box">
        <Roles :id="data.role?.code" @change="onSelectRole" />

        <div v-if="data.role?.code" class="role-detail">
            <RoleInfo :role="data.role"/>
            <Users :role="data.role"/>
        </div>
    </div>
</template>

<script setup>
    import { reactive } from 'vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import PageHeader from '@/components/blocks/PageHeader/PageHeader.vue';
    import Roles from '@/components/blocks/Roles.vue';

    import RoleInfo from './components/RoleInfo.vue';
    import Users from './components/Users.vue';
    
    import { useI18n } from 'vue-i18n';

    const { t } = useI18n();

    const data = reactive({
        role: null
    });

    const onSelectRole = (role) => {
        data.role = null;
        setTimeout(() => {
            data.role = role?.code ? role : null;
        });
    }
</script>

<style lang="scss" scoped>
    .box {
        display: flex;
        flex-direction: row;
        align-content: start;
        gap: 48px;
        padding: 0 48px;
        flex: 1 1 100%;

        .role-detail {
            display: flex;
            flex-direction: column;
            gap: 32px;
            padding-top: 24px;
            flex: 1 1 100%;
        }
    }
</style>
