<template>
    <AutoComplete
        v-bind="props"

        :optionList="data.optionList || []"
        @search="onAutoCompleteSearch"
        @change="emit('change', $event)"
    >
        <template #option="{ option }">
            <span class="user-option-preview">
                <Img v-if="option.profileImage?.resized"
                    :preview="option.profileImage?.resized"
                    :original="option.profileImage?.original"
                    :alt="option?.name"
                />
                <span class="name">{{ option.name }}</span>
                <span class="username">@{{ option.userName }}</span>
            </span>
        </template>
        <template #value="{ value }">
            <span class="user-value-preview">
                <Img v-if="value.profileImage?.resized"
                    :preview="value.profileImage?.resized"
                    :original="value.profileImage?.original"
                    :alt="value?.name"
                />
                <span>
                    <span class="name">{{ value.name }}</span>
                    <span class="username">@{{ value.userName }}</span>
                </span>
            </span>
        </template>
    </AutoComplete>
</template>

<script setup>
    import { reactive, onMounted } from 'vue';
    import AutoComplete from 'ðŸ”—/components/ui/AutoComplete.vue';
    import Img from 'ðŸ”—/components/ui/Img.vue';
    import api from 'ðŸ”—/api';

    const emit = defineEmits(['change']);

    const props = defineProps({
    });

    const data = reactive({
        optionList: []
    });

    const onAutoCompleteSearch = async (value) => {
        const searchStr = value?.toLowerCase().trim();

        const params = {
            skip: 0,
            take: 30,
            sort: 'firstName',
            sort_dir: 'asc'
        }

        if (searchStr?.trim()) {
            params.firstName = searchStr.trim().toLowerCase();
        }

        const payload = await api.users().list(params);

        if (!payload?.items) {
            data.optionList = [];
            return;
        }

        data.optionList = await payload.items.map(item => ({
            code: item.id,
            name: item.firstName,
            userName: item.userName,
            profileImage: item.profileImage
        }));
    }

    onMounted(() => {
        onAutoCompleteSearch();
    })
</script>


<style lang="scss" scoped>
    .user-option-preview {
        @include user-option-preview;
    }

    .user-value-preview {
        @include user-value-preview;
    }
</style>
